const createProfilesList = () => {
    const profiles = []
    const myProfile = {
        aboutMe: 'About me',
        contacts: {
        facebook: 'https://facebook.com',
        website: 'https://mywebsite.com',
        vk: 'https://vk.com',
        twitter: 'https://twitter.com',
        instagram: 'https://instagram.com',
        youtube: 'https:/youtube.com',
        github: 'https://github.com/al-trapeznikov743',
        mainLink: 'https://mainlink.com'
        },
        lookingForAJob: true,
        lookingForAJobDescription: 'JavaScript, React, Redux, REST API and some...',
        fullName: `Alexandr Trapeznikov`,
        userId: 12619,
        photos: {
        small: small,
        large: large
        }
    }
    for(let i = 0; i < userNames.length; i++) {
        profiles.push({
            aboutMe: 'About me',
            contacts: {
            facebook: 'https://facebook.com',
            website: 'https://mywebsite.com',
            vk: 'https://vk.com',
            twitter: 'https://twitter.com',
            instagram: 'https://instagram.com',
            youtube: 'https:/youtube.com',
            github: 'https://github.com',
            mainLink: 'https://mainlink.com'
            },
            lookingForAJob: false,
            lookingForAJobDescription: 'my skills',
            fullName: `${userNames[i]} Sanchez`,
            userId: i+1,
            photos: {
            small: null,
            large: null
            }
        })
    };
    return [...profiles, myProfile]
}

export const getProfileApi = (userId) => {
    const profiles = createProfilesList()
    const profile = profiles.filter(item => item.userId === +userId)
    return {
        data: profile[0]
    }
}

export const getStatusApi = (userId) => {
    const users = createUsersList()
    const user = users.filter(item => item.id === +userId)
    return {
        data: user[0].status
    }
}

export const setProfileApi = (profile) => {
    const {
        fullName,
        aboutMe, 
        lookingForAJobDescription,
        lookingForAJob,
        contacts} = profile
    const profiles = createProfilesList()
    const prevProfile = profiles.filter(item => item.userId === 12619)
    const nextProfile = {
        ...prevProfile[0],
        fullName: fullName || prevProfile[0].fullName,
        aboutMe: aboutMe || prevProfile[0].aboutMe,
        lookingForAJobDescription: lookingForAJobDescription || prevProfile[0].lookingForAJobDescription,
        lookingForAJob: lookingForAJob || prevProfile[0].lookingForAJob,
        contacts: contacts || prevProfile[0].contacts
    }
    return {
        data: nextProfile
    }
}