export const createUsersList = () => {
    const users = []
    const me = {
        name: 'al_trapeznikov',
        id: 12619,
        photos: {
        small: small,
        large: large
        },
        status: "It's working",
        followed: true
    }
    for(let i = 0; i < userNames.length; i++) {
        users.push({
            name: userNames[i],
            id: i+1,
            photos: {
            small: null,
            large: null
            },
            status: 'My status',
            followed: false
        })
    }
    return [...users, me]
}

export const getUsersApi = (pageNumber, pageSize) => {
    const start = (pageNumber-1)*pageSize
    const end = start + +pageSize
    const totalCount = userNames.length

    const usersList = createUsersList()

    const response = {
        data: {
            items: usersList.filter(item => item.id > start && item.id < end+1),
            totalCount: totalCount,
            error: null
        }
    }
    return response
}