import React, {useState} from 'react'
import {Formik, Form, Field} from 'formik'
import styles from '../loginForm/LoginForm.module.sass'
import * as yup from 'yup'
import {NavLink} from 'react-router-dom'


const RegistrationForm = (props) => {

    const [errorMessage, setErrorMessage] = useState(null)

    const validationSchema = yup.object().shape({
        name: yup.string().required('Required field'),
        fullName: yup.string().required('Required field'),
        email: yup.string().required('Required field'),
        password: yup.string().required('Required field')
    })

    const onSubmit = async (formData, {setSubmitting, resetForm}) => {

        // change
        const {email, password, rememberMe} = formData
        const errorMessage = await props.login(email, password, rememberMe)


        if (errorMessage) {
            setErrorMessage(errorMessage)
            resetForm()
        }
        setSubmitting(false)
        resetForm()
    }
    return <div className={`${styles.form_wrapper} element`}>
        <Formik
            initialValues={{name: '', fullName: '', email: '', password: ''}}
            validationSchema={validationSchema}
            onSubmit={onSubmit}
        >
            {({
                isSubmitting,
                isValid,
                touched,
                errors
            }) => (
                <Form className={styles.form}>
                    <>
                        <Field
                            type='text'
                            name='name'
                            placeholder='Your nick name'
                            className={`${styles.form_item} ${styles.field}`}
                        />
                        {touched.name && errors.name &&
                            <span className={styles.error}>
                                Field is required
                            </span>}
                        <Field
                            type='text'
                            name='fullName'
                            placeholder='Your full name'
                            className={`${styles.form_item} ${styles.field}`}
                        />
                        {touched.fullName && errors.fullName &&
                            <span className={styles.error}>
                                Field is required
                            </span>}
                        <Field
                            type='text'
                            name='email'
                            placeholder='Email'
                            className={`${styles.form_item} ${styles.field}`}
                        />
                        {touched.email && errors.email &&
                            <span className={styles.error}>
                                Field is required
                            </span>}
                        <Field
                            type='text'
                            name='password'
                            placeholder='Password'
                            className={`${styles.form_item} ${styles.field}`}
                        />
                        {touched.email && errors.email &&
                            <span className={styles.error}>
                                Field is required
                            </span>}
                        <button
                            type='submit'
                            disabled={isSubmitting || !isValid}
                            className={`${styles.form_item}`}>
                            Sign up
                        </button>
                    </>
                    <div className={styles.create_ac}>
                        <NavLink to='/login'>
                            <span className={styles.create}>Come back</span>
                        </NavLink>
                        {errorMessage && <span className={styles.error}>{errorMessage}</span>}
                    </div>
                </Form>
            )}
        </Formik>
    </div>
}

export default RegistrationForm